@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  background-color: #c4c4c4;
  margin: auto;
  min-height: 100vh;
  max-width: 1440px;
}

#root {
  background-color: #ffffff;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

body *:not(span) {
  font-family: Montserrat, "sans-serif" !important;
  font-size: 16px !important;
  font-weight: normal !important;
}

.headline {
  font-family: Montserrat, "sans-serif" !important;
  font-size: 24px !important;
  font-weight: normal !important;
}

.title {
  font-family: Montserrat, "sans-serif" !important;
  font-size: 20px !important;
  font-weight: normal !important;
}

.subtitle {
  font-family: Montserrat, "sans-serif" !important;
  font-size: 14px !important;
  font-weight: bold !important;
}

.button-label {
  font-family: Montserrat, "sans-serif" !important;
  font-size: 14px !important;
  font-weight: bold !important;
}

.primary-color {
  color: #005b95 !important;
}

/* @media (width > 960px) == 33% */
.pag-inicial {
  text-align: center;
  width: calc(100% / 3);
}

/* PHONE LANDSCAPE + TABLET == 67% */
@media (max-width: 960px) {
  .pag-inicial {
    width: calc(200% / 3);
  }
}

/* PHONE PORTRAIT == 100% */
@media (max-width: 599px) {
  .pag-inicial {
    width: 100%;
  }
}

.pag-inicial > :first-child {
  margin-bottom: 8vmin;
}

.pag-inicial .busca > :first-child {
  margin-bottom: 6vmin;
}

.pag-cadastro {
  background-color: #c4c4c4;
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-cadastro {
  background-color: #ffffff;
  padding: 16px;
  min-width: 65vw;
}

.form-cadastro header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.form-cadastro .form {
  max-width: 40%;
  margin-bottom: 40px;
}

/* PHONE + TABLET */
@media (max-width: 960px) {
  .form-cadastro .form {
    max-width: 100%;
  }
}

.form-cadastro .form .campo-form {
  margin-top: 20px;
}

.form-cadastro .form button[name="adicionarInteressado"] {
  float: right;
}

.form-cadastro footer button {
  float: right;
  background: #005b95;
}

.pag-resultado {
  width: calc(100% - 80px);
  min-height: calc(100vh - 40px);
  margin-top: 40px;
}

.pag-resultado header {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.pag-resultado header .busca {
  width: 45%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.pag-resultado header .busca .title {
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
}

.pag-resultado header .busca #inputBusca {
  width: 100%;
  margin-left: 20px;
}

.pag-resultado header #btnNovo {
  margin-left: 20px;
}

.pag-resultado .conteudo {
  display: flex;
  flex-direction: row;
}

.pag-resultado #loadingBusca {
  padding-left: 30vw;
  padding-top: 15vh;
}

/* PHONE LANDSCAPE + TABLET */
@media (max-width: 900px) {
  .pag-resultado header .busca {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .pag-resultado .conteudo {
    display: flex;
    flex-direction: column;
  }
}

/* PHONE PORTRAIT */
@media (max-width: 600px) {
  .pag-resultado header {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .pag-resultado header .busca {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .pag-resultado header #btnNovo {
    width: 100%;
    margin-left: 0;
  }
}

.listagem {
  width: 100%;
}

.listagem .card-processo {
  margin: 20px 0;
  cursor: pointer;
}

.listagem .card-processo .atual {
  border: 2px solid #005b95;
  box-sizing: border-box;
}

.listagem .card-processo table {
  table-layout: fixed;
  width: 100%;
  /* white-space: nowrap; */
}

.listagem .card-processo table th {
  vertical-align: bottom;
}

.listagem .card-processo table th,
.listagem .card-processo table td {
  text-align: left;
  padding: 2px 8px;
}

.listagem .card-processo table td {
  vertical-align: top;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.listagem .card-processo .avatar {
  width: 84px;
  height: 84px;
}

.listagem .card-processo #avatarListagem {
  width: 84px;
  padding-left: 2px;
}

.detalhes {
  width: calc(55 * 100% / 45);
  margin-top: 20px;
}

.detalhes #paperDetalhes {
  margin-left: 20px;
  padding: 16px;
}

.detalhes header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.detalhes header table {
  table-layout: fixed;
  width: 100%;
  margin-left: -4px;
}

.detalhes header table #avatarDetalhes {
  width: 9vw;
}

.detalhes .avatar {
  height: 8vw;
  width: 8vw;
}

.detalhes .conteudo {
  display: flex;
  flex-direction: column;
  padding-bottom: 32px;
}

.detalhes .conteudo .interessados ul {
  padding-left: 0;
}

.detalhes footer {
  text-align: end;
}

.detalhes footer > * {
  margin-left: 20px;
}

/* PHONE LANDSCAPE + TABLET */
@media (max-width: 900px) {
  .detalhes {
    width: 100%;
    margin-bottom: 20px;
  }
  .detalhes #paperDetalhes {
    margin-left: 0;
  }
}

